<template>
  <div
    class="color-select"
    :style="{ background: currentBgColor }"
  >
    <span
      class="current-color"
      :class="{ 'is-expand': isExpand }"
      :style="{ background: currentBgColor }"
      @click="isExpand = !isExpand"
    ></span>
    <ul
      class="color-wrapper"
      v-show="isExpand"
    >
      <li
        v-for="(c, i) in colors"
        :key="'color_' + i"
        :style="{ background: c }"
        class="color-item"
        @click="changeBgColor(c)"
      ></li>
    </ul>
  </div>
  <ul
    class="list-wrapper"
    :style="{ background: currentBgColor }"
  >
    <li
      class="cube-item"
      v-for="(e, i) in data"
      :key="'cube_' + i"
      :class="{ actived: activedItem == i}"
      @click="hanlderCubeClick(i)"
    >
      <div>
        <img
          :src="getImage(e.icon) || cube_logo"
          @click.stop="viewImage(e.icon)"
        />
        <span class="cube-name">{{ e.name || e.index }}</span>
      </div>
      <div class="formulas">
        <p
          v-for="(m, j) in e.formulas"
          :key="'formula_' + j"
        >
          <span class="formula-index">公式{{ j + 1 }}:</span>
          <span class="formula">{{ m }}</span>
        </p>
      </div>
      <img
        class="drop-btn"
        src="~@/assets/icon/drop-down.png"
        alt=""
      >
    </li>
    <li class="end-tip">
      <span
        class="tip-text"
        :style="{ background: currentBgColor }"
      >
        end
      </span>
      <div class="divider"></div>
    </li>
  </ul>
  <div
    class="view-img"
    v-if="imgDialog"
  >
    <img
      :src="viewImageItem || cube_logo"
      alt=""
    >
    <div
      class="mask"
      @click="imgDialog = false"
    ></div>
  </div>
</template>

<script>
import data from 'assets/json/rubiks-cube.json'

export default {
  name: 'Formula',
  data() {
    return {
      data,
      cube_logo: require('assets/images/rubik-cube-logo.png'),
      viewImageItem: '',
      imgDialog: false,
      activedItem: -1,
      colors: [
        '#FFF',
        '#FAF9DE',
        '#FFF2E2',
        '#FDE6E0',
        '#E3EDCD',
        '#DCE2F1',
        '#E9EBFE',
        '#EAEAEF',
      ],
      isExpand: false,
      currentBgColor: '#FFF',
    }
  },
  created() {
    const bgColor = localStorage.getItem('BG_COLOR')
    this.currentBgColor = bgColor || this.colors[0]
  },
  methods: {
    getImage(path) {
      const img = require('assets/images/' + path)
      return img
    },
    viewImage(path) {
      this.viewImageItem = this.getImage(path)
      this.imgDialog = true
    },
    hanlderCubeClick(index) {
      this.activedItem = this.activedItem == index ? -1 : index
    },
    changeBgColor(c) {
      this.isExpand = false
      this.currentBgColor = c
      localStorage.setItem('BG_COLOR', c)
    },
  },
}
</script>

